.App {
    font-family: Arial, sans-serif;
  }
  
  .search {
    width: 1150px;
    min-width: 1150px;
    max-width: 1150px;
    border-radius: 10px;
    height: 30px;
    padding: 5px;
    font-size: 15px;
  
  }
  
  .button {
    background-color: #6BDD97;
    border-radius: 5px;
    padding: 10px 20px;
    color: white;
    margin-bottom: 40px;
  
  }
  
  
  .button:hover {
    transform: scale(1.1);
    transition: transform 0.4s ease, box-shadow 0.2s ease;
    box-shadow: 0 10px 20px rgba(32, 23, 23, 0.245); 
  }
  
  .title1 {
     
    font-size: 1.5em;                 
    font-weight: bold;                
    color: #6BDD97;           
    text-align: left;               
    margin-top: 20px;                
    margin-bottom: 20px; 
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  
  }